import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async () => {
    const {data: {data : {movies}}} = await axios.get("https://yts-proxy.now.sh/list_movies.json");  // 사이트에서 데이터 가져오기 & 데이터를 가져오는데 시간이 걸리니 기다리기
    this.setState({movies, isLoading:false})
  };
  componentDidMount() {
    this.getMovies();          // 데이터를 다 가져오면 componetDidMount 실행
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader_text">Loding...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
          )}
      </section>
    );
  }
}

export default App;
